import React,{useRef,useEffect,useState} from 'react'
import Layout from '../common/Layout';

function Location() {
    const mapContainer = useRef(null);
    const placesList = useRef(null);
    const page = useRef(null);
    const {kakao} = window; //     (ESLint )  kakao     .
    const [render,setRender] = useState(null);
    const [traffic, setTraffic] = useState(false);
    const path = process.env.PUBLIC_URL;
    const [index,setIndex] = useState(null);
    let markers = [];
    const [word,setWord] = useState(" ")
    const [place, setPlace] = useState(" ")
    const input = useRef(null)

    const positions = [
        {
            title: '', 
            latlng: new kakao.maps.LatLng(37.47545222269596, 126.61779809042052),
            offset : {offset: new kakao.maps.Point(-40,70)}
        },
        {
            title: ' ', 
            latlng: new kakao.maps.LatLng(37.4746247, 126.630497),
            offset : {offset: new kakao.maps.Point(35,55)}
        }
    ];
    
    useEffect(()=>{
        
        mapContainer.current.innerHTML=''
        const mapOption = {
            center: new kakao.maps.LatLng(37.566826, 126.9786567), //  
            level: 3 //   
        };  

        //      
        const map = new kakao.maps.Map(mapContainer.current, mapOption); 
        // //    
        const ps = new kakao.maps.services.Places();  
        // //          
        const infowindow = new kakao.maps.InfoWindow({zIndex:1});
        //   
        searchPlaces();

        //    
        function searchPlaces() {
            const keyword = place;

            if (!keyword.replace(/^\s+|\s+$/g, '')) {
                alert(' !');
                return false;
            }
            //      
            ps.keywordSearch(keyword, placesSearchCB); 
            setRender(map)
        }



        //      
        function placesSearchCB(data, status, pagination) {
            if (status === kakao.maps.services.Status.OK) {

                //   
                //    
                displayPlaces(data);

                //   
                displayPagination(pagination);

            } else if (status === kakao.maps.services.Status.ZERO_RESULT) {

                alert('   .');
                return;

            } else if (status === kakao.maps.services.Status.ERROR) {

                alert('    .');
                return;

            }
        }
        //      
        function displayPlaces(places) {

            const listEl = placesList.current,
            //menuEl = document.getElementById('menu_wrap'),
            fragment = document.createDocumentFragment(), 
            bounds = new kakao.maps.LatLngBounds(), 
            listStr = '';
            
            //      
            removeAllChildNods(listEl);

            //     
            removeMarker();
            
            for ( let i=0; i<places.length; i++ ) {

                //    
                const placePosition = new kakao.maps.LatLng(places[i].y, places[i].x),
                    marker = addMarker(placePosition, i), 
                    itemEl = getListItem(i, places[i]); //    Element 

                //       
                // LatLngBounds   
                bounds.extend(placePosition);

                //    mouseover 
                //     
                // mouseout    
                (function(marker, title) {
                    kakao.maps.event.addListener(marker, 'mouseover', function() {
                        displayInfowindow(marker, title);
                    });

                    kakao.maps.event.addListener(marker, 'mouseout', function() {
                        infowindow.close();
                    });

                    itemEl.onmouseover =  function () {
                        displayInfowindow(marker, title);
                    };

                    itemEl.onmouseout =  function () {
                        infowindow.close();
                    };
                })(marker, places[i].place_name);

                fragment.appendChild(itemEl);
            }

            //     Element 
            listEl.appendChild(fragment);
            //menuEl.scrollTop = 0;

            //       
            map.setBounds(bounds);
        }
        //   Element  
        function getListItem(index, places) {

            let el = document.createElement('li'),
            itemStr = '<span class="markerbg marker_' + (index+1) + '"></span>' +
                        '<div class="info">' +
                        '   <h5>' + places.place_name + '</h5>';

            if (places.road_address_name) {
                itemStr += '    <span>' + places.road_address_name + '</span>' +
                            '   <span class="jibun gray">' +  places.address_name  + '</span>';
            } else {
                itemStr += '    <span>' +  places.address_name  + '</span>'; 
            }
                        
            itemStr += '  <span class="tel">' + places.phone  + '</span>' +
                        '</div>';           

            el.innerHTML = itemStr;
            el.className = 'item';

            return el;
        }

        //       
        function addMarker(position, idx, title) {
            const imageSrc = 'https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/marker_number_blue.png', //   url,   
                imageSize = new kakao.maps.Size(36, 37),  //   
                imgOptions =  {
                    spriteSize : new kakao.maps.Size(36, 691), //   
                    spriteOrigin : new kakao.maps.Point(0, (idx*46)+10), //       
                    offset: new kakao.maps.Point(13, 37) //      
                },
                markerImage = new kakao.maps.MarkerImage(imageSrc, imageSize, imgOptions),
                    marker = new kakao.maps.Marker({
                    position: position, //  
                    image: markerImage 
                });

            marker.setMap(map); //    
            markers.push(marker);  //    

            return marker;
        }

        //       
        function removeMarker() {
            for ( let i = 0; i < markers.length; i++ ) {
                markers[i].setMap(null);
            }   
            markers = [];
        }

        //      
        function displayPagination(pagination) {
            let paginationEl = page.current,
                fragment = document.createDocumentFragment(),
                i; 

            //    
            while (paginationEl.hasChildNodes()) {
                paginationEl.removeChild (paginationEl.lastChild);
            }

            for (i=1; i<=pagination.last; i++) {
                let el = document.createElement('a');
                el.href = "#";
                el.innerHTML = i;

                if (i===pagination.current) {
                    el.className = 'on';
                } else {
                    el.onclick = (function(i) {
                        return function() {
                            pagination.gotoPage(i);
                        }
                    })(i);
                }

                fragment.appendChild(el);
            }
            paginationEl.appendChild(fragment);
        }

        //        
        //   
        function displayInfowindow(marker, title) {
            const content = '<div style="padding:5px;z-index:1;">' + title + '</div>';

            infowindow.setContent(content);
            infowindow.open(map, marker);
        }

        //    Element  
        function removeAllChildNods(el) {   
            while (el.hasChildNodes()) {
                el.removeChild (el.lastChild);
            }
        }

    },[place])

    useEffect(()=>{
        if(index !== null) {
            mapContainer.current.innerHTML=''
            const options = { 
                center: positions[index].latlng,
                level: 3
            };
            const map = new kakao.maps.Map(mapContainer.current, options);

            
            //---------------------------------------
            const imageSrc = path+'marker/marker2.png'; 
            const imageOption = positions[index].offset;
            const imageSize = new kakao.maps.Size(60, 60) 
            const markerImage = new kakao.maps.MarkerImage(imageSrc, imageSize, imageOption)

            new kakao.maps.Marker({
                    map: map, 
                    position: positions[index].latlng, 
                    title : positions[index].title, 
                    image : markerImage 
            });   
            setRender(map)

            function setCenter() {            
                const moveLatLon = positions[index].latlng;
                map.setCenter(moveLatLon);
            }
            window.addEventListener("resize", setCenter);

            const mapTypeControl = new kakao.maps.MapTypeControl();
            map.addControl(mapTypeControl, kakao.maps.ControlPosition.TOPRIGHT);
            const zoomControl = new kakao.maps.ZoomControl();
            map.addControl(zoomControl, kakao.maps.ControlPosition.RIGHT);


            return ()=>{window.removeEventListener("resize",setCenter);}
        }

        
    },[index])

    //     ''    .  map    if 
    useEffect(()=>{
       if(render) {
        (traffic) ? render.addOverlayMapTypeId(kakao.maps.MapTypeId.TRAFFIC) : render.removeOverlayMapTypeId(kakao.maps.MapTypeId.TRAFFIC);
       }
    },[traffic])

    
  return (
    <Layout>
        <h2> </h2>
        <div className="map-wrap">
            <div id="map" ref={mapContainer}></div>
            
            <div id="menu_wrap" class="bg_white">
                <div class="option">
                    <div>
                        <form onSubmit={(e)=>{
                            e.preventDefault()
                            setPlace(word)
                            input.current.focus()
                        }}>
                             : <input type="text" value={word} id="keyword" size="15" onChange={(e)=>setWord(e.target.value)} ref={input}/> 
                            <button type="submit"></button> 
                        </form>
                    </div>
                </div>
                <hr/>
                <ul id="placesList" ref={placesList}></ul>
                <div id="pagination" ref={page}></div>
            </div>
        </div>
        <p>     .</p>
        <div className="btn">
            {/*   */}
            <button onClick={()=>{setTraffic(!traffic)}}>{(traffic) ? ' ' : '  '}</button>
            {/*   */}
            {positions.map((el,idx)=>{
                return <button key={idx} onClick={()=>{
                    setIndex(idx)
                    setWord('')
                    input.current.focus()
                }}>{el.title}</button>
            })}
        </div>
    </Layout>

  )
}

export default Location